@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  src: local(''),
    url('inter-v7-latin_cyrillic-regular.woff2') format('woff2'),
    url('inter-v7-latin_cyrillic-regular.woff') format('woff'),
    url('inter-v7-latin_cyrillic-regular.eot'),
    url('inter-v7-latin_cyrillic-regular.eot?#iefix') format('embedded-opentype'),    
    url('inter-v7-latin_cyrillic-regular.ttf') format('truetype'),
    url('inter-v7-latin_cyrillic-regular.svg#Inter') format('svg');
}

